.horizonal-menu-bar {
  overflow: auto;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  margin: .2em auto;
}

.horizonal-menu-bar a {
  display: inline-block;
  text-align: center;
  padding: .5em;
  color: #333;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 4px solid rgba(255, 255, 255, 0);
  -webkit-transition: border .25s, background-color .25s;
  -o-transition: border .25s, background-color .25s;
  transition: border .25s, background-color .25s;
  cursor: pointer;
}

.horizonal-menu-bar a:hover {
  background-color: #eee;
  text-decoration: none;
  border-bottom: 4px solid rgba(0, 156, 255, 1);
}

.horizonal-menu-bar a.active {
  border-bottom: 4px solid rgba(0, 156, 255, 1);
}




