.data-field {
  font-family: Arial, sans-serif;
}

.data-field thead td {
  text-align: center;
}

.data-field tbody td {
  text-align: right;
}

.td-category {
  text-align: center;
}

.tablerow-max .val {
  color: orangered;
  font-weight: bold;
}

.tablerow-min .val {
  color: steelblue;
  font-weight: bold;
}

.delta-up {
  color: orange;
}

.delta-down {
  color: steelblue;
}

.delta-equal {
  color: gray;
}


